<template>
  <div id="invate">
    <div class="bg" style=""></div>
    <van-nav-bar title="我的邀请" fixed style="z-index:1000">
      <van-icon name="arrow-left" slot="left" color="black" @click.native="$router.go(-1)"/>
    </van-nav-bar>
    <div id="container" style="z-index:999;margin-top:40px">
      <van-tabs v-model="active" sticky :offset-top="40"  v-on:click="stairAgency">
        <van-tab  title="I 级合伙人">
          <div v-for="items in messages" style="margin-top:0.3rem;display: flex;justify-content: space-between;padding:0.8rem 1rem;background-color: #ffffff">
            <p style="font-size: 16px">{{items.mobile}}</p>
            <p style="font-size: 16px">{{items.nickname}}</p>
          </div>
        </van-tab>
        <van-tab title="II 级合伙人">
          <div v-for="item in twoArray">
            <div v-for="items in item" style="margin-top:0.3rem;display: flex;justify-content: space-between;padding:0.8rem 1rem;background-color: #ffffff">
              <p style="font-size: 16px">{{items.mobile}}</p>
              <p style="font-size: 16px">{{items.nickname}}</p>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
  export default {
    name: "invate",
    data() {
      return {
        active: 'true',
        activeNames: ['1'],
        loading: false,
        uid: '',
        pageNum: 1,
        pageSize: 10,
        inviteList: [],
        messages:[],
        twoArray:[],
        baseUrl:'',
        invitePeople:[
          {title:"I 级合伙人"},
          {title:"II 级合伙人"}
        ],
      }
    },
    created() {
      this.stairAgency(0,)
    },
    methods: {
      stairAgency(index,title){
        if( index === 1 ){
          this.secondLevel()
        }else if( index === 0 ){
          this.baseUrl = "oagency"
        }
        let self = this;
        this.$POST('/user/' + this.baseUrl,true).then(res=>{
          console.log(res)
          if(res.code === 200){
            self.messages = res.data;
          }
        }).catch(res=>{
          console.log(res)
          this.$toast('服务器连接失败')
        })
      },
      secondLevel(){
        var self = this;
        this.$POST('/user/tagency',true).then(res=>{
          console.log(res)
          if(res.code === 200){
            self.twoArray = res.data;
          }
        }).catch(res=>{
          console.log(res)
          this.$toast('服务器连接失败')
        })
      },
    },
  }
</script>

<style scoped>
  #invate {
    font-size: 1rem;
    color: #000000;
    font-family: PingFang-SC-Regular;
    width: 100%;
    height: 100%;
    background-color: #ededed;
  }

  p {
    margin: 0px;
  }

  .icon img {
    width: 2rem;
    height: 2rem;
    border-radius: 2rem;
  }

  .title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 100%;
    width: 100%;
  }

  .title .left_title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
  }

  .title .right_title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: -15px;
    color: #ff681f;
  }

  .title p {
    margin: 0px;
  }

  .title > div {
    margin-left: 1rem;
  }

  .title .name {
    margin-top: -8px;
  }

  .title .wx {

  }

  .title .in {
    float: right;
  }

  .right_icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 5px;
  }

  .container {
    color: #999999;
  }

  #container .item {
    margin-top: 0.4rem;
  }

  .container .people .box {
    padding: 0 0rem;
  }

  .container .reward {
    margin-top: 0.5rem;
    border-top: 1px solid #ededed;
    padding-top: 0.5rem;
  }

  .container .reward .box {
    padding: 0rem 0rem;
  }

  .container span {
    display: inline-block;
    padding: 0.3rem;
  }

</style>



